
<style scoped>
.h-center {
  margin-left: auto;
  margin-right: auto;
}

.starting {
  text-align: center;
  color: gray;
  margin-top: 200px;
}

.progress {
  font-size: 40px;
}
</style>


<template>
  <div class="h-center starting">
    <div class="h-center logo">
      <img src="/logo.png" />
      <h3>请稍候</h3>
    </div>
    <div class="h-center progress">
      <i class="el-icon-loading"></i>
    </div>
    <div v-show="debug_mode">
      <pre> {{currentSession}} </pre>
    </div>
  </div>
</template>

<script>
import { setTimeout } from "timers";

var env = process.env.NODE_ENV;
var debug_enable = env == "development";

export default {
  name: "starting",

  data() {
    return {
      debug_mode: false,
      timer_done: false,
      net_done: false,
      session: null
    };
  },

  created() {
    this.net_done = false;
    this.timer_done = false;
    this.session_ok = false;
    this.refresh();
  },

  mounted() {
    this.setTimer(1000 * 2);
    this.debug_mode = debug_enable;
  },

  computed: {
    currentSession() {
      var session = this.$store.getters["CurrentSession/session"];
      return JSON.stringify(session, null, 4);
    }
  },

  methods: {
    refresh() {
      var self = this;

      var on_ok = resp => {
        self.onSessionStateReady(resp);
      };

      var on_error = res => {
        self.showError(res);
      };

      var type = "CurrentSession";
      var id = null;

      this.$store.dispatch("rest/get", { id, type, on_error, on_ok });
      this.$store.dispatch("CurrentSession/refresh", {});
    },

    setTimer(ms) {
      var self = this;
      setTimeout(() => {
        self.onTimer();
      }, ms);
    },

    onTimer() {
      this.timer_done = true;
      this.tryJump();
    },

    onSessionStateReady(res) {
      var state = res.currentSession;
      this.session_ok = state.authenticated;
      this.net_done = true;
      this.tryJump();
    },

    showError(res) {
      var js = JSON.stringify(res, null, 4);
      this.$message("error: " + js);
    },

    tryJump() {
      if (this.net_done && this.timer_done) {
        if (this.session_ok) {
          this.jumpToHome();
        } else {
          this.jumpToLogin();
        }
      }
    },

    jumpToLogin() {
      this.$router.push("login");
    },

    jumpToHome() {
      this.$router.push("home");
    }
  }
};
</script>
